import React, {useState, useEffect} from 'react'
import Axios from 'axios';
import {Card ,  CardHeader, CardBody } from 'reactstrap';

function Todos() {

    const [todos , setTodos] = useState([]);

    const getTodos = () => {
        Axios
            .get('https://jsonplaceholder.typicode.com/todos')
            .then(res => setTodos(res.data))
            .catch(err => console.log(err))
    }

    useEffect(() => {
        getTodos();
    }, []);

    return (
        <div>
            {todos.map(todo => 
                <Card className="mt-3" key={todo.id} outline color="dark">
                    <CardHeader className="bg-dark text-warning">User {todo.id}</CardHeader>
                    <CardBody>
                        <span className="font-weight-bold">Subject Todo :</span><p>{todo.title}</p>
                        {todo.completed ? <div><span className="font-weight-bold">Status :</span><p className="text-success">Completed</p></div> 
                        : <div><span className="font-weight-bold">Status :</span><p className="text-danger">Not completed</p></div>}
                    </CardBody>
                </Card>    
            )}
        </div>
    )
}

export default Todos
